
.board {
  width: 20vw;
  height: 100%;
  background: #A3C6C4;
  margin: 35px;
  border-radius: 10px;
  font-size: 36px;
  font-weight: bold;
  color: #354649;
}
.board a {
    color: #354649;
}
.btn {
  background: #354649;
}
.btn:hover {
  background: #6C7A89;
}
.btn:focus {
  background: #354649;
}
.btn-floating {
  background: #354649;
}
.btn-floating:hover {
  background: #6C7A89;
}
.btn-floating:focus {
  background: #354649;
}



